import React, { useState } from "react";
import { useHistory } from "react-router";
import fb from "../../assets/img/logo-social/fb.png";
import tw from "../../assets/img/logo-social/tw.png";
import tel from "../../assets/img/logo-social/tel.png";
import ig from "../../assets/img/logo-social/ig.png";
import line from "../../assets/img/logo-social/line.png";
function Navbar(props) {
  const history = useHistory();
  const [selectNav, setSelectNav] = useState(props.select);
  console.log(selectNav);
  const pageHome = () => {
    setSelectNav("home");
    history.push("/home");
  };
  const pagePortfolios = () => {
    history.push("/portfolios");
  };
  const pageProjects = () => {
    history.push("/projects");
  };
  const pageContact = () => {
    history.push("/contact");
  };
  return (
    <div className="nav-box">
      <div className="nav-content"></div>
      <div
        className="nav-head "
        onClick={() => {
          pageHome();
        }}
      >
        Anucha Leurach
      </div>
      <div
        className="nav-sub"
        onClick={() => {
          pageHome();
        }}
      >
        {selectNav == "home" ? (
          <div className="nav-select">Home</div>
        ) : (
          <div> Home</div>
        )}
      </div>

      <div
        className="nav-sub"
        onClick={() => {
          pagePortfolios();
        }}
      >
        {selectNav == "portfilios" ? (
          <div className="nav-select">Portfolios</div>
        ) : (
          <div> Portfolios</div>
        )}
      </div>
      <div
        className="nav-sub"
        onClick={() => {
          pageProjects();
        }}
      >
        {selectNav == "projects" ? (
          <div className="nav-select">Projects</div>
        ) : (
          <div> Projects</div>
        )}
      </div>
      <div
        className="nav-sub"
        onClick={() => {
          pageContact();
        }}
      >
        {selectNav == "contact" ? (
          <div className="nav-select">Contact</div>
        ) : (
          <div> Contact</div>
        )}
      </div>
      <div className="nav-sub">|</div>
      <div className="nav-icons">
        <a href="https://www.facebook.com/123456.4444j">
          <img src={fb} />
        </a>
      </div>
      <div className="nav-icons">
        <a href="https://line.me/ti/p/NFrGpMMBlQ">
          <img src={line} />
        </a>
      </div>
      <div className="nav-icons">
        <a href="https://www.instagram.com/kao.anucha/">
          <img src={ig} />
        </a>
      </div>
    </div>
  );
}

export default Navbar;
