import React from "react";
import fb from "../../assets/img/logo-social/fb.png";
import tw from "../../assets/img/logo-social/tw.png";
import tel from "../../assets/img/logo-social/tel.png";
import ig from "../../assets/img/logo-social/ig.png";
import line from "../../assets/img/logo-social/line.png";
import { useHistory } from "react-router";

import ReactTooltip from "react-tooltip";

function Footer() {
  const history = useHistory();
  const home = () => {
    history.push("home");
  };
  const portfolios = () => {
    history.push("portfolios");
  };
  const project = () => {
    history.push("projects");
  };
  const contact = () => {
    history.push("contact");
  };
  return (
    <div className="footer">
      <div className="text-center">
        <a href="https://www.facebook.com/123456.4444j">
          <img src={fb} />
        </a>
        &emsp;&emsp;
        <a href="https://line.me/ti/p/NFrGpMMBlQ">
          <img src={line} />
        </a>
        &emsp;&emsp;
        <a href="https://www.instagram.com/kao.anucha/">
          <img src={ig} />
        </a>
        &emsp;&emsp;
          <a data-tip="tel : 088-6584669">
            <img src={tel} />
          </a>
          <ReactTooltip place="right" type="light" effect="float" />
       
        <div className="footer-nav">
          <p onClick={() => home()}>Home</p>&emsp;:&emsp;
          <p onClick={() => portfolios()}>Portfolios</p>&emsp;:&emsp;
          <p onClick={() => project()}>Projects</p>&emsp;:&emsp;
          <p onClick={() => contact()}>Contact</p>
          &emsp;&emsp;Copyright © 2021 Anucha Luhrach
        </div>
      </div>
    </div>
  );
}

export default Footer;
