import React, { useState } from "react";
import Footer from "../fragments/Footer";
import Navbar from "../fragments/Navbar";
import fby from "../../assets/img/logo-social/fby.png";
import tely from "../../assets/img/logo-social/tely.png";
import liney from "../../assets/img/logo-social/liney.png";
import maily from "../../assets/img/logo-social/maily.png";
import mail from "../../assets/img/logo-social/mail.png";
import ReactTooltip from "react-tooltip";
import contact from "../../assets/img/contact.png";
function Contact() {

  const [dataContact, setDataContact] = useState({
    name: "",
    address: "",
    email: "",
    tel: "",
    message: ""
  });
  const submit_contact = () => {
    setDataContact({
      name: "",
      address: "",
      email: "",
      tel: "",
      message: ""
    })
    window.location.reload();
  }
  const handleChange = e => {
    const { name, value } = e.target;
    setDataContact(prevState => ({
        ...prevState,
        [name]: value
    }));
};
console.log(dataContact)
  return (
    <div>
      <Navbar select="contact" />
      <div className="box-contact">
        <div className="content-contact">
          <div className="content-contact-l">
            <img src={contact} alt="" />
          </div>
          <div className="content-contact-r">
            <div className="row ">
              <div className="col-md-4"></div>
              <div className="col-md-6">
                <div className="contact-title">
                  <b>
                    <h2>Contact</h2>
                  </b>
                  <img src={mail} alt="" />
                </div>
              </div>
            </div>

            <br />
            <br />
            <div className="row ">
              <div className="col-md-2"></div>
              <div className="col-md-2">
                <h5 className="text-right">ชื่อ : </h5>
              </div>
              <div className="col-md-6">
                <input
                  type="text"
                  className="form-control"
                  placeholder="ชื่อ - นามสกุล หรือ ชื่อเล่น"
                  value={dataContact.name}
                  onChange={handleChange}
                  name="name"
                />
              </div>
            </div>
            <br />
            <div className="row ">
              <div className="col-md-2"></div>
              <div className="col-md-2">
                <h5 className="text-right">ที่อยู่ : </h5>
              </div>
              <div className="col-md-6">
                <input
                  type="text"
                  className="form-control"
                  placeholder="ที่อยู่"
                  value={dataContact.address}
                  onChange={handleChange}
                  name="address"
                />
              </div>
            </div>
            <br />
            <div className="row ">
              <div className="col-md-2"></div>
              <div className="col-md-2">
                <h5 className="text-right">อีเมล : </h5>
              </div>
              <div className="col-md-6">
                <input
                  type="email"
                  className="form-control"
                  placeholder="อีเมล"
                  value={dataContact.email}
                  onChange={handleChange}
                  name="email"
                />
              </div>
            </div>
            <br />
            <div className="row ">
              <div className="col-md-2"></div>
              <div className="col-md-2">
                <h5 className="text-right">เบอร์โทร : </h5>
              </div>
              <div className="col-md-6">
                <input
                  type="text"
                  className="form-control"
                  placeholder="เบอร์โทร"
                  value={dataContact.tel}
                  onChange={handleChange}
                  name="tel"
                />
              </div>
            </div>
            <br />
            <div className="row ">
              <div className="col-md-2"></div>
              <div className="col-md-2">
                <h5 className="text-right">ข้อความ : </h5>
              </div>
              <div className="col-md-6">
                <textarea
                  type="text"
                  className="form-control"
                  placeholder="ข้อความ"
                  value={dataContact.message}
                  onChange={handleChange}
                  name="message"
                />
              </div>
            </div>
            <br />
            <div className="row ">
              <div className="col-md-4"> </div>
              <div className="col-md-6">
                <button type="submit" className="btn btn-warning form-control"
                  onClick={() => submit_contact()}
                >
                  <b>Submit</b>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="box-contact-sc">
        <u>
          <h3 className="text-center">ช่องทางการติดต่อ</h3>
        </u>
        <br />

        <div className="contact-item">
          <a data-tip="email : lanuchal.lkaol@gmail.com">
            <div className="contact-item-contants">
              {"{ "}
              <div>
                <img src={maily} />
                <br />
                <p>Email</p>
              </div>
              {" }"}
            </div>
          </a>
          <ReactTooltip place="right" type="light" effect="float" />
          <a href="https://www.facebook.com/123456.4444j">
            <div className="contact-item-contants">
              {"{ "}
              <div>
                <img src={fby} />
                <br />
                <p>FB</p>
              </div>
              {" }"}
            </div>
          </a>
          <a href="https://line.me/ti/p/NFrGpMMBlQ">
            <div className="contact-item-contants">
              {"{ "}
              <div>
                <img src={liney} />
                <br />
                <p>Line</p>
              </div>
              {" }"}
            </div>
          </a>
          <a data-tip="tel : 088-6584669">
            <div className="contact-item-contants">
              {"{ "}
              <div>
                <img src={tely} />
                <br />
                <p>Tel</p>
              </div>
              {" }"}
            </div>

            <ReactTooltip place="right" type="light" effect="float" />
          </a>
        </div>
        <br />
        <br />
        <p className="text-center">
          ที่อยู่ : 37/2 หมู่ 7 ตำบลแม่สัน อำเภอห้างฉัตร จังหวัดลำปาง 52190
        </p>
      </div>
      <Footer />
    </div>
  );
}

export default Contact;
